<template>
    <div class="main">
        <div class="drillManAdd">
            <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
                <el-tab-pane label="演练分配" name="first">
                    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                        <el-form-item label="演练名称" prop="title">
                            <el-input v-model.trim="ruleForm.title" style="width:300px" placeholder="请输入演练名称"></el-input>
                        </el-form-item>
                        <!-- 人员任务开始 -->
                        <el-form-item label="人员任务" v-for="(domain) in dynamicValidateForm.domains" :key="domain.key" prop="">
                            <el-select v-model="domain.user_id" clearable filterable placeholder="请选择人员" @change="peopleChange()">
                                <el-option v-for="item in teacherList" :disabled="item.disabled" :label="item.teacher_name" :value="item.teacher_id" :key="item.teacher_id"></el-option>
                            </el-select>
                            <el-input v-model.trim="domain.task" style="width:300px" placeholder="请输入人员任务内容"></el-input>
                            <el-button @click.prevent="removeDomain(domain)">删除</el-button>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" plain @click="addDomain">添加</el-button>
                            <!-- <el-button type="primary" plain @click.prevent="removeDomain(domain)">删除</el-button> -->
                        </el-form-item>

                        <!--人员任务结束  -->

                        <el-form-item label="场景" prop="scene">
                            <el-select v-model="ruleForm.scene" placeholder="请选择场景">
                                <el-option v-for="item in drillList" :label="item.dictionary_name" :value="Number(item.dictionary_value)" :key="item.dictionary_value"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item style="margin-left:10%">
                            <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
                            <!-- <el-button type="primary" @click="resetForm('ruleForm')">取消</el-button> -->
                        </el-form-item>
                    </el-form>
                </el-tab-pane>
                <el-tab-pane label="铃声配置" :disabled='disable' name="second" style="background-color: #ffffff;padding-left:10px;margin-bottom:50px">
                    <ringtone-configuration-1 v-if="activeName === 'second'"></ringtone-configuration-1>
                </el-tab-pane>
                <el-tab-pane label="班牌配置" :disabled='disable' name="third">
                    <deckArrangement1></deckArrangement1>
                </el-tab-pane>
                <el-tab-pane label="屏幕配置" :disabled='disable' name="fourth">
                    <screenConfiguration1></screenConfiguration1>
                </el-tab-pane>
                <el-tab-pane label="预警灯配置" :disabled='disable' name="fifth">
                    <switchMent></switchMent>
                </el-tab-pane>
            </el-tabs>
            <div class="box">
                <el-divider></el-divider>
                <el-footer class="footer" border>
                    <el-button type="primary" @click="backDrill">演练管理</el-button>
                </el-footer>
            </div>
        </div>
    </div>

</template>
<script>
import ringtoneConfiguration1 from "./component/ringtoneConfiguration.vue"
import deckArrangement1 from "./component/deckArrangement.vue"
import screenConfiguration1 from "./component/screenConfiguration.vue"
import earlyWarningLight1 from "./component/earlyWarningLight.vue"
import switchMent from "./component/switchMent.vue"
import { drillManagementAddApi, hashApi, drillView, viewTaskApi, drillManagementEditApi } from "@/api/index"
export default {
    name: "drillManAdd",
    components: {
        ringtoneConfiguration1, deckArrangement1, screenConfiguration1, earlyWarningLight1, switchMent
    },
    data() {
        return {
            activeName: 'first',
            ruleForm: {
                title: '',
                scene: '',
                domains: [{
                    user_id: "",
                    task: ''
                }],
            },
            rules: {
                title: [
                    { required: true, message: '请输入演练名称', trigger: 'blur' },
                    { max: 200, message: '长度在200个字符内', trigger: 'blur' }
                ],
                scene: [
                    { required: true, message: '请选择场景', trigger: 'change' }
                ],
                user_id: [
                    { required: true, message: '请选择人员', trigger: 'change' }
                ],
                task: [
                    { required: true, message: '请输入任务', trigger: 'change' }
                ],
                name: [
                    { required: true, message: '请输入任务', trigger: 'change' }
                ],
            },
            //添加的人员
            dynamicValidateForm: {
                domains: [{
                    user_id: "",
                    task: ''
                }],
                email: ''
            },
            //弹层隐藏
            centerDialogVisible: false,
            //铃声设置数据
            tableData: [],
            disable: true,
            teacherList: [],
            drillList: [],
        };
    },
    mounted() {
        if (this.$route.params.id) {
            this.getInfo(this.$route.params.id);
            this.disable = false;
        } else if (localStorage.getItem('Rehearsal_Id')) {
            this.getInfo(JSON.parse(localStorage.getItem('Rehearsal_Id')));
            this.disable = false;
        }
        this.resView();
    },
    methods: {
        backDrill() {
            this.$router.push({ name: "drillManagement" })
        },
        async getInfo(id) {
            const res = await this.axios.post(viewTaskApi, { id: id });
            if (res.data.code == 1) {
                this.ruleForm = res.data.data;
                this.dynamicValidateForm.domains = res.data.data.list;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        goBack() {
            this.$router.go(-1);
        },
        peopleChange() {
            let arr = [];
            this.dynamicValidateForm.domains.forEach(ele => {
                arr.push(ele.user_id)
            })
            this.teacherList.forEach(item => {
                if (arr.includes(item.teacher_id)) {
                    item.disabled = true;
                } else {
                    item.disabled = false;
                }
            })
        },
        async resView() {
            const res = await this.axios.post(drillView);
            if (res.data.code == 1) {
                res.data.data.teacher.forEach(item => {
                    item.disabled = false;
                });
                this.teacherList = res.data.data.teacher;
                this.drillList = res.data.data.scene_list
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },

        handleClick(tab, event) {
            this.activeName = tab.name;
        },

        async getHash() {
            const res = await this.axios.post(hashApi, { page_hash: 'drillManAdd' });
            if (res.data.code == 1) {
                this.hash = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        submitForm() {
            this.$refs['ruleForm'].validate(async valid => {
                if (valid) {
                    await this.getHash();
                    let data = {
                        title: this.ruleForm.title,
                        task_data: JSON.stringify(this.dynamicValidateForm.domains),
                        scene: this.ruleForm.scene,
                        hash: this.hash,
                    }
                    let api;
                    if ((localStorage.getItem('type') == 'edit') || (localStorage.getItem('type') == 'add' && localStorage.getItem('Rehearsal_Id'))) {
                        data.id = JSON.parse(localStorage.getItem('Rehearsal_Id'));
                        api = drillManagementEditApi;
                    } else {
                        api = drillManagementAddApi;
                    }
                    const res = await this.axios.post(api, data);
                    if (res.data.code == 1) {
                        //存储演练信息ID
                        localStorage.setItem('Rehearsal_Id', JSON.stringify(res.data.rehearsal_id))
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'success'
                        });
                        this.dialogFormVisible = false;
                        this.disable = false;
                        this.activeName = "second"
                    } else {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'error'
                        });
                    }

                }
            })

        },
        resetForm() {
            this.ruleForm = {
                title: '',
                scene: '',
                name: "",
                task: "",
                domains: [{
                    user_id: "",
                    task: ''
                }],
            };
            this.$nextTick(() => {
                this.$refs["ruleForm"].resetFields();
            })

        },
        //新增人员
        addDomain() {
            this.ruleForm.domains = this.dynamicValidateForm.domains.push({
                user_id: '',
                task: '',
            });

        },
        //删除人员
        removeDomain(item) {
            if (this.dynamicValidateForm.domains.length > 1) {
                this.$confirm('是否确定进行删除', '删除提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                })
                    .then(() => {
                        this.teacherList.forEach(ele => {
                            if (item.user_id == ele.teacher_id) {
                                ele.disabled = false;
                            }
                        })
                        var index = this.dynamicValidateForm.domains.indexOf(item)
                        if (index !== 0) {
                            this.dynamicValidateForm.domains.splice(index, 1)
                        }
                    })
                    .catch(err => {
                        this.$notify.info({
                            title: '取消',
                            message: '取消操作'
                        });
                    });
            }
        },
        //弹层确认
        removeDomainDel(item) {
        },


        // 铃声配置开始
        //铃声预览
        musicPre() {
            console.log("播放");
        },
        delMusic() {
            console.log("delMusic-删除配置音乐",);
        }

    }
};
</script>
<style lang="scss" scoped>
.school_check {
    height: 60px;
    padding: 0 24px;
    background: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.drillManAdd {
    position: relative;
    width: 100%;
    height: 100%;
    .el-tabs--card > .el-tabs__header .el-tabs__item.is-active {
        background-color: #78beff;
        color: #fff;
    }
    .box {
        position: absolute;
        width: 100%;
        height: 180px;
        bottom: 0px;
        padding: 0px;
        margin: 0px;
        .footer {
            text-align: center;
            padding: 20px 20px;
        }
    }
}
</style>


