<template>
    <div>
        <el-button @click.prevent="musicAdd" style="margin:20px 0;background-color:#169bd5;color:#fff">新增预警灯配置</el-button>
        <div class="home_table">
            <el-table :data="tableData" border style="width: 100%;">
                <template v-for="(item, index) in tableData">
                    <el-table-column :prop="item.prop" :label="item.title" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true"></el-table-column>
                </template>
                <el-table-column fixed="right" label="铃声预览" width="200">
                    <template slot-scope="scope">
                        <el-button @click="musicPre(scope.row)" type="text" size="small">铃声预览</el-button>
                    </template>
                </el-table-column>
                <el-table-column fixed="right" label="操作" width="200">
                    <template slot-scope="scope">
                        <el-button @click="handleClick(scope.row)" type="text" size="small">编辑</el-button>
                        <el-button @click="delMusic(scope.row)" type="text" size="small">删除</el-button>
                    </template>
                </el-table-column>

            </el-table>
        </div>
        <!-- 分页 -->
        <div class="pagination">
            <el-pagination background :current-page.sync="page" :page-sizes="[15, 30, 50, 100]" :page-size="pageSize" layout="prev, pager, next, sizes,total" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<15? true:false" />
        </div>
        <!-- 编辑弹层 -->
        <el-dialog title="编辑提示" :visible.sync="centerDialogVisibleEit" width="30%">
            <!-- <span>是否确定进行删除</span> -->
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="活动区域" prop="region">
                    <el-select v-model="ruleForm.region" multiple filterable allow-create default-first-option placeholder="请选择文章标签">
                        <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="铃声预览" prop="">
                    <el-input v-model="ruleForm.name" style="width:auto;"></el-input>
                    <el-button size="mini" style="border:0;vertical-align: middle;">
                        <i class="el-icon-circle-close" style="color: red;font-size:xx-large"></i>
                    </el-button>
                    <span>时间</span>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer" style="text-align:right">
                <el-button @click="handleCloseEsc">取 消</el-button>
                <el-button type="primary" @click="removeDomainDelEit">确 定</el-button>
            </span>
        </el-dialog>

        <!-- 删除弹层 -->
        <el-dialog title="删除提示" :visible.sync="centerDialogVisible" width="30%" center>
            <span>是否确定进行删除</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="centerDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="removeDomainDel">确 定</el-button>
            </span>
        </el-dialog>
    </div>

</template>

<script>
export default {
    name: "ringtoneConfiguration",
    data() {
        return {
            //铃声设置数据
            tableData: [],

            //编辑弹层数据
            centerDialogVisible: false,
            centerDialogVisibleEit: false,
            ruleForm: {
                name: '',
                region: '',
            },
            rules: {
                name: [
                    { required: true, message: '请输入活动名称', trigger: 'blur' },
                    { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                ],
                region: [
                    { required: true, message: '请选择活动区域', trigger: 'change' }
                ],
            },

            options: [{
                value: 'HTML',
                label: 'HTML'
            }, {
                value: 'CSS',
                label: 'CSS'
            }, {
                value: 'JavaScript',
                label: 'JavaScript'
            }],
            value: []
        }
    },
    methods: {
        //新增铃声配置
        musicAdd() {
            this.$router.push({ name: 'earlyWarningLightAdd' })
        },

        //点击编辑
        handleClick() {
            this.centerDialogVisibleEit = true
        },
        //编辑弹层确定按钮
        removeDomainDelEit() {
            this.centerDialogVisibleEit = false
        },
        //编辑弹层取消按钮
        handleCloseEsc() {
            // console.log("确定按钮");
            this.centerDialogVisibleEit = false,
                this.$refs.ruleForm.resetFields()
        },

        //点击删除
        delMusic() {
            this.centerDialogVisible = true
        },
        //弹层确认框
        removeDomainDel() {
            this.centerDialogVisible = false
            //删除
            console.log("删除了");

        }
    }

}
</script>

<style lang="scss" scoped>
</style>


